import { Box, makeStyles } from '@material-ui/core';
import crud from '../Asset/Images/crud.png'

const useStyle = makeStyles({
    image: {
        width: '50%',
        height: '50%',
        margin: '80px 0 0 345px'
    }
})

const LandingPage = () => {
    const classes = useStyle();
    return (
        <Box>
            <img className={classes.image} src={crud} />
            <div  style={{marginTop:"150px"}}>
                <marquee><h1  style={{color: "red"}} >What is a CRUD App</h1></marquee>
                <p>A CRUD app is a specific type of software application that consists of four basic operations; Create, Read, Update, Delete.

                    At a high level, a CRUD apps consist of three parts; the database, user interface, and APIs.</p>
                    <marquee><h1  style={{color: "red"}}>DataBase</h1></marquee>
                <p>The database is where your data is stored. A database management system is used to manage the database. There are several different types of database management systems (DBMS) that can be categorized by how they store data; relational (SQL), Document (NoSQL). If you are deciding what DBMS to choose for your CRUD app, hereâ€™s a list of the 14 best database management systems . Going forward, our content will focus on SQL. SQL databases consist of tables. Tables consist of records. Records consist of fields. Fields consist of data.</p>
                <marquee><h1  style={{color: "red"}}>CRUD operations</h1></marquee>
                <p>As previously mentioned, there are four basic CRUD operations; create, read, update and delete. </p>
            </div>
        </Box>

    )
}

export default LandingPage;