import { FormGroup, FormControl, Input, InputLabel, Button,makeStyles,Typography } from "@material-ui/core";

const useStyle = makeStyles({
    container:{
        width:'50%',
        margin:'5% 0 5% 25%',
        '& > *':{
            marginTop:'10px',
            
        }
        
    }
})

const AddUser = () => {
    const classes = useStyle();
    return (
        <FormGroup className={classes.container}>
            <Typography variant='h4'>Add User</Typography>
            <FormControl>
                <InputLabel>Name</InputLabel>
                <Input type="text" />
            </FormControl>
            <FormControl>
                <InputLabel>Id</InputLabel>
                <Input type="number" />
            </FormControl>
            <FormControl>
                <InputLabel>Class</InputLabel>
                <Input type="number" />
            </FormControl>
            <FormControl>
                <InputLabel>CNIC</InputLabel>
                <Input type="number" />
            </FormControl>
            <FormControl>
                <InputLabel>Father Name </InputLabel>
                <Input type="text" />
            </FormControl>
            <FormControl>
                <InputLabel>Gender</InputLabel> 
                <Input type="text" />
            </FormControl>
            <FormControl>
                <InputLabel>Date of Birth</InputLabel>
                <br />
                <br />
                <Input type="date" />
            </FormControl>
            <FormControl>
                <InputLabel>Email</InputLabel>
                <Input type="email" />
            </FormControl>
            <FormControl>
                <InputLabel>Phone number</InputLabel>
                <Input type="number" />
            </FormControl>
            <FormControl>
                <InputLabel>Address</InputLabel>
                <Input type="text" />
            </FormControl>
            <Button variant='contained' style={{background:'#8B0000',color:'white'}}>Add User</Button>
        </FormGroup>
    )
}

export default AddUser;
